@import "../../global.scss";

.intro{
    background-color: rgb(255, 255, 255);
    display: flex;
    

    @include mobile{
        flex-direction: column;
        align-items: center;

    }


    .left{
        flex:0.5;
        overflow: hidden;

        .imgContainer{
            height: 700px;
            display: flex;
            justify-content: flex-end;
            align-items: center;
            float: right;
            border-radius: 50%;

            @include mobile{
                align-items: flex-start;
                flex:1;
                height:350px;
            }

            img{
                height: 90%;

                @include mobile{
                    height: 80%;
                }
            }
        }

    }
    .right{
        flex:0.5;


        .wrapper{
            width:100%;
            height:100%;
            padding-left: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            position: relative;

            @include mobile{
                padding-left: 0;
                align-items: center;
            }


            h1{
                font-size: 60px;
                margin: 10px 0;


                @include mobile{
                    font-size: 40px;
                }

            }
            h2{
                font-size: 35px;
                @include mobile{
                    font-size: 20px;
                }
                
            }
            h3{
                font-size: 30px;
                
                span{
                    font-size: inherit;
                    color: rgb(0, 0, 0);
                }

                .ityped-cursor{
                  animation: blink 1s infinite;  
                }
                @keyframes blink {
                    50%{
                        opacity: 1;
                    }
                    100%{
                        opacity: 0;
                    }
                }

            }
        }
        
        a{
            position: absolute;
            bottom: 10px;
            left: 50%;

            img {
                width: 30px;
                animation: arrowBlink 2s infinite;
            }
            @keyframes arrowBlink {
                100%{
                    opacity: 0;
                }
            }
        }
    }
}
